@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,400i,600,700&subset=cyrillic,cyrillic-ext");
.button {
  display: flex;
  align-items: center;
  padding: 0;
  line-height: 1;
  border-radius: 0;
  position: relative;
  border: 1px solid transparent;
  box-sizing: border-box;
  background-color: transparent;
  transition: background-color 0.45s cubic-bezier(0.3, 1, 0.4, 1) 0s;
  /* @Sizes */
  /* @Accents */ }
  .button:hover {
    cursor: pointer; }
  .button:focus {
    outline: none; }
  .button:disabled {
    opacity: .5; }
    .button:disabled:hover {
      cursor: not-allowed; }
  .button_xs {
    font-size: 12px;
    /* &_outline, */ }
    .button_xs_default {
      padding: 0 8px;
      min-height: 24px;
      border-radius: 2px; }
  .button_md {
    font-size: 14px;
    /* &_outline, */ }
    .button_md_default {
      padding: 0 12px;
      min-height: 32px;
      border-radius: 3px; }
  .button_default_bostonBlue {
    color: #fff;
    background-color: #2e92bd; }
    .button_default_bostonBlue:hover:enabled {
      background-color: #13a1de; }
    .button_default_bostonBlue:focus {
      background-color: #2e92bd;
      box-shadow: 0 0 5px 0 #2e92bd; }
  .button_default_atomic {
    color: #fff;
    background-color: #3c4b53; }
    .button_default_atomic:hover:enabled {
      background-color: #354148; }
    .button_default_atomic:focus {
      background-color: #354148;
      box-shadow: 0 0 5px 0 #354148; }



