.wrapper {
  margin-top: 40px;
  padding: 0 10px; }

.input_invalid {
  box-shadow: inset 0 0 0 1px #eb5353, 0 0 4px 0 #eb5353; }
  .input_invalid:-moz-placeholder {
    color: #eb5353; }
  .input_invalid::-moz-placeholder {
    color: #eb5353; }
  .input_invalid:-ms-input-placeholder {
    color: #eb5353; }
  .input_invalid::-webkit-input-placeholder {
    color: #eb5353; }

.container {
  max-width: 600px;
  margin: 0 auto;
  display: flex;
  flex-direction: column; }
  .container__title {
    font-size: 22px;
    margin-bottom: 18px; }
  .container__form {
    display: flex;
    align-items: flex-start; }
  .container__button {
    flex-shrink: 0;
    margin-left: 8px; }
  .container + .section {
    margin-top: 40px; }

.section {
  max-width: 600px;
  margin: 0 auto; }
  .section__heading {
    display: block;
    font-size: 26px;
    text-align: center; }
  .section__title {
    line-height: 1; }
  .section__content {
    margin: 10px 0;
    padding: 10px 5px;
    border-top: 1px solid rgba(255, 255, 255, 0.05); }
  .section__footer {
    margin: 10px 0;
    padding: 10px 0; }
  .section__cell {
    display: block; }

.history__list {
  margin: 10px 0;
  padding: 0;
  list-style-type: none; }

.history__item {
  display: flex;
  align-items: center;
  padding: 8px 10px;
  margin: 5px 0;
  border-radius: 2px; }
  .history__item:nth-child(odd) {
    background-color: rgba(255, 255, 255, 0.01); }
  .history__item:nth-child(even) {
    background-color: rgba(255, 255, 255, 0.05); }

.history__caption {
  text-transform: capitalize;
  flex-grow: 1;
  padding-right: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.history__btn {
  margin-left: auto;
  min-width: 100px;
  justify-content: center; }



