@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,400i,600,700&subset=cyrillic,cyrillic-ext");
.wrapper {
  width: 100%;
  min-height: 32px; }

.container {
  position: relative;
  min-height: inherit; }

.field {
  width: 100%;
  min-height: inherit;
  padding: 0 10px;
  border: 0;
  line-height: normal;
  border-radius: 2px;
  box-sizing: border-box;
  font-size: 14px;
  text-overflow: ellipsis;
  -webkit-appearance: none !important;
  transition: box-shadow 0.45s cubic-bezier(0.3, 1, 0.4, 1) 0s; }
  .field:focus {
    outline: none;
    padding-right: 25px; }
    .field:focus + .btn {
      opacity: 1; }
  .field:-moz-placeholder {
    opacity: 1;
    line-height: normal;
    text-overflow: ellipsis; }
  .field::-moz-placeholder {
    opacity: 1;
    line-height: normal;
    text-overflow: ellipsis; }
  .field:-ms-input-placeholder {
    opacity: 1;
    line-height: normal;
    text-overflow: ellipsis; }
  .field::-webkit-input-placeholder {
    opacity: 1;
    line-height: normal;
    text-overflow: ellipsis; }
  .field_invalid {
    box-shadow: inset 0 0 0 1px #e75454, 0 0 4px 0 #e75454; }

.btn {
  padding: 0;
  border: 0;
  opacity: 0;
  outline: none;
  right: 8px;
  top: 50%;
  border-radius: 50%;
  transform: translateY(-50%);
  position: absolute;
  background-color: rgba(204, 204, 204, 0.5);
  transition: opacity 0.45s cubic-bezier(0.3, 1, 0.4, 1) 0s, background-color 0.45s cubic-bezier(0.3, 1, 0.4, 1) 0s; }
  .btn:hover {
    cursor: pointer;
    background-color: #ccc; }
  .btn:before {
    content: '\2715';
    color: #fff;
    font-size: 8px;
    width: 12px;
    height: 12px;
    display: flex;
    align-items: center;
    justify-content: center; }

.error {
  display: block;
  margin-top: 8px;
  font-size: 14px;
  color: #e75454; }



