@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,400i,600,700&subset=cyrillic,cyrillic-ext");
html {
  height: 100%;
  overflow: hidden; }

body {
  height: 100%;
  overflow: auto;
  margin: 0;
  font-size: 16px;
  line-height: 1.2;
  font-family: "Source Sans Pro", sans-serif;
  color: #fff;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

:global #app {
  height: 100%; }

:global h1 {
  color: #fff; }

:global .span {
  color: #fff;
  font-weight: 600; }

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #171717; }

.content {
  margin: 0 auto;
  width: 100%;
  max-width: 992px;
  display: flex;
  flex-grow: 1;
  flex-direction: column; }



